* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.containerMoleInHole9 {
    display: grid;
    width: 100%;
    height: 100vw;
    max-width: 600px;
    max-height: 600px;
    gap: 5px;
    padding: 5px;
    grid-template-areas:
        'i1 i2 i3'
        'i4 i5 i6'
        'i7 i8 i9';
    align-items: center;
}

.containerMoleInHole16 {
