    height: 100vw;
    max-width: 600px;
    max-height: 600px;
    gap: 5px;
    padding: 5px;
    grid-template-areas:
        'i1 i2 i3 i4'
        'i5 i6 i7 i8'
        'i9 i10 i11 i12'
        'i13 i14 i15 i16';
    align-items: center;
}

.containerMoleInHole25 {
